@font-face {
    font-family: "peyda";
    src: url("./font/woff2/PeydaWeb-Medium.woff2") format("woff2"),
    url("./font/woff/PeydaWeb-Medium.woff") format("woff");
    font-weight: medium;
}
@font-face {
    font-family: "peyda";
    src: url("./font/woff2/PeydaWeb-Bold.woff2") format("woff2"),
    url("./font/woff/PeydaWeb-Bold.woff") format("woff");
    font-weight: bold;
}
@tailwind utilities;

:root {
    --white: #ffffff;
    --primary: #0E5395;
    --secondary: #0E9594;
    --descript: #B1C4D7;
    --red: #FF3D00;
    --yellow: #FFC107;
    --green: #4D9833;
  }

body{
    font-family: 'peyda', 'sans-serif', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana;
}

.active {
    color: var(--secondary);
}

.active > div > svg > path {
    fill: var(--secondary);
}

.pcOnly {
    display: flex;
}
.mobileOnly {
    display: none;
}

/* Navbar */








































.faqItem {
    margin-top: 2rem;
}

.faqItem:hover {
    margin-top: 1rem;
}

.homeServiceCarouselItem > h4 {
    margin-top: 1rem;
    margin-bottom: 0rem;
}

.homeServiceCarouselItem:hover > h4 {
    margin-top: 0rem;
    margin-bottom: 1rem;
}

.homeServiceCarouselItemIcon {
    top: -2.5rem;
}

.homeServiceCarouselItem:hover > .homeServiceCarouselItemIcon {
    top: -5.2rem;
}

.homeServiceCarouselItemIcon > svg > path {
    transition: all .5s ease;
}

.homeServiceCarouselItem:hover > .homeServiceCarouselItemIcon > svg > path {
    fill: #0E9594;
}

.homePortfolioItemHover {
    transition: all .3s ease;
    opacity: 0;
}

.homePortfolioItem:hover > .homePortfolioItemHover{
    opacity: 0.9;
}

.homePortfolioItem:hover > img {
    transform: scale(1.2);
}

.homePlansItem > .homePlansItemHover {
    transform-origin: top;
    transform: scaleY(.05)
}

.homePlansItem:hover > .homePlansItemHover {
    transform: scale(1);
}

.homePlansItemLink:first-child > .homePlansItem > .homePlansItemHover {
    background: linear-gradient(to bottom right, #D67C33 0%, #FACFAC 45%, #FACFAC 55%, #D67C33 100%);
}

.homePlansItemLink:nth-child(2) > .homePlansItem > .homePlansItemHover {
    background: linear-gradient(to bottom right, #8C8C8C 0%, #F9F9F9 45%, #F9F9F9 55%, #8C8C8C 100%);
}

.homePlansItemLink:nth-child(3) > .homePlansItem > .homePlansItemHover {
    background: linear-gradient(to bottom right, #FDC930 0%, #F8EAC3 45%, #F8EAC3 55%, #FDC930 100%);
}

.homeQaItem {
    margin: 1rem 0;
}

.homeQaItem > input {
    display: none;
}

.homeQaItem > .homeQaItemActive {
    transition: all .3s ease;
    display: none;
}

.homeQaItem > input:checked ~ label {
    color: #0E9594;
}

.homeQaItem > input:checked ~ .homeQaItemActive {
    display: flex;
    color: #759292;
}

.homeQaItem > label svg > circle,
.homeQaItem > label svg > path {
    transition: all .3s ease;
}

.homeQaItem > input:checked ~ label svg > circle {
    fill: #0E9594;
    opacity: .1;
}

.homeQaItem > input:checked ~ label svg > path {
    fill: #0E9594;
}

