/* Animations */

/* Home Hero Animation */
@keyframes animRect {
    0% {}
    50% {
        left: 15%;
        bottom: 90%;
        transform: rotate(-45deg);
    }
    100% {
        left: 10%; 
        bottom: 10%;
        transform: rotate(-90deg);
    }
}

.animRect{
    position: absolute;
    left: 10%; 
    bottom: 10%;
    animation: animRect 30s linear 0s infinite forwards;
}

@keyframes animCircle {
    0% {}
    25% {
        transform: scale(1.5);
    }
    50% {
        right: 15%;
        bottom: 60%;
        transform: rotate(-45deg);
    }
    75% {
        transform: scale(1.5);
    }
    100% {
        right: 30%; 
        bottom: 20%;
        transform: rotate(-90deg);
    }
}

.animCircle{
    position: absolute;
    right: 30%; 
    bottom: 20%;
    animation: animCircle 25s linear 0s infinite forwards;
}

@keyframes animPlus {
    0% {}
    25% {
    }
    50% {
        right: 30%;
        bottom: 10%;
        transform: rotate(-45deg);
    }
