        bottom: 70%;
        transform: rotate(-90deg);
    }
}

.animPlus{
    position: absolute;
    right: 40%; 
    bottom: 70%;
    animation: animPlus 43s linear 0s infinite forwards;
}

@keyframes animTriangle {
    0% {}
    25% {
    }
