.navbar {
    z-index: 40;
    position: fixed;
    top: 0;
    right: 1rem;
    left: 1rem;
    border-bottom-right-radius: 1rem;
    border-bottom-left-radius: 1rem;
}

.navbarBG {
    border-bottom-right-radius: 1rem;
    border-bottom-left-radius: 1rem;
}

.navbarLogo {
    order: 0;
}

.navbarMenu {
    position: relative;
    display: flex;
    flex-direction: row;
    width: 100%;
}

.navbarItem {
    margin: 0 .5rem;
    padding: 1rem 1rem;
}

.megaMenu {
    position: absolute;
    top: 100%;
    right: 0;
    bottom: 0;
    left: 0;
    min-height: 500px;
    display: flex;
    transform-origin: top;
    transform: scale(1, 0);
    transition: all .3s ease;
    background-color: white;
}
.navbarItem:hover > .megaMenu {
    transform: scale(1, 1);
}
.chevronActive {
    transform: rotate(0deg);
}

.navItemIcon > svg > path {
    fill: var(--primary);
}
.navbarMenuItemChild:hover > .navItemContent {
    color: var(--secondary);
}
.navbarMenuItemChild:hover > .navItemIcon > svg > path {
    fill: var(--secondary);
}

.navbarMenuPortfolioItemChild:hover img {
    transform: scale(1.2);
}
.navPortfolioItemContent {
    opacity: 0;
}
.navbarMenuPortfolioItemChild:hover .navPortfolioItemContent {
    opacity: .95;
}




.navbarSupport:hover > a > span:nth-child(1){
    letter-spacing: 0.07rem;
}
.navbarSupport:hover > a > span:nth-child(2){
    transform: rotate(45deg);
}

/* only Landscape views */
@media only screen and (orientation:landscape) {
    .navbarItem:hover .navbarItemTitle > button {
        color: var(--secondary);
    }
    .navbarItem:hover .chevronLeft {
        transform: rotate(-90deg);
        color: var(--secondary);
    }
}

/* only Portrait views */
@media only screen and (orientation:portrait) {
    .pcOnly {
        display: none;
    }
    .mobileOnly {
        display: flex;
    }

    .navbar {
        position: fixed;
        right: 0;
        left: 0;
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
    }
    
    .navbarBG {
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
    }

    .navbarLogo {
        order: 1;
    }

    .navbarMenu {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        padding: 8rem 0 0 0;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        transform-origin: top right;
        transform: scale(0, 1);
        transition: all .3s ease;
        background-color: var(--white);
        overflow-y: scroll;
    }

    .navbarMenu.show {
        transform: scale(1, 1);
    }

    .navbarItem {
        width: 100%;
        margin: .5rem 0rem;
        padding: 0;
    }

    .chevronActive {
        color: var(--secondary);
        transform: rotate(-90deg);
    }

    .megaMenu {
        position: static;
        min-height: auto;
        display: none;
        margin: 0rem 0;
        transform: scale(1, 1);
    }
    .megaMenu.show {
        display: flex;
    }






}




